.fullscreen {
    position: fixed;
    display: block;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: 0.5s;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.1);
    z-index: 10;
}
.sidebarContainer{
    position: fixed;
    top:0;
    background-color: #404040;
    height: 100vh;
    width: 35vw;
    z-index: 11;
    transition: 0.2s ease-in;
    overflow-x: hidden;

}
.fi-xtluxl-times-thin {
    color: #ffffff;
    float: right;
    font-size: 3rem;
    font-weight: 600;
    padding-top: 2rem;
    padding-right: 2rem;
    cursor: pointer;
 }
 .fi-xtluxl-times-thin:hover{
     color: rgb(156, 156, 156);
 }

 /* Sidebar For Mobile Phones*/
 @media screen and (max-width:767px){
     .sidebar-container{
         width: 100%;
     }
 }
 .sidebarContainer ul{
     margin-top: 20vh;
     display: flex;
     flex-direction: column;
     list-style: none;
 }

 .sidebarContainer ul li {
     color: #ffffff;
     font-family:'Varela Round', sans-serif;
     padding-left: 5rem;
     padding: 1rem 2rem;
     /* border-bottom: 1px #dddddd solid;
     border-top: 1px #dddddd solid; */
     width: 300px;
 }
 .sidebarContainer ul li:hover{
     background-color: #ffffff;
     color: #404040;
     cursor: pointer;
 }

.sidebarContainer ul li i{
    font-size: 1.3rem;
    padding: 0 1rem;
}
